.login-section {
  width: 400px;
}
.text-usm {
  letter-spacing: 0.3em;
  font-size: 15px;
}

.text-spacing {
  letter-spacing: 0.5em;
}

.btn-login {
  background-color: #49516b;
  color: white;
  width: 100%;
  margin-top: 20px;
}

.input-with-icon {
  position: relative; /* To position the icon relative to this container */
  // display: flex; /* Align the icon and input horizontally */
  align-items: center; /* Center align the items vertically */
}

.input-icon {
  width: 30px; /* Set the width of the SVG icon */
  height: 30px; /* Set the height of the SVG icon */
  margin-right: 8px; /* Add some space between the icon and the input field */
}

.forgot-password {
  margin-left: 30px;
}
